.phonebook {
    min-width: 600px;
    min-height: 1000px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    color: #000000;

    background-image: url('/src/img/pexels-cottonbro-studio-4065906.jpg');
        background-size: cover;
        background-position: center bottom;
        /* положення фону по центру горизонталі та унизу вертикалі */
        background-repeat: no-repeat;
        background-attachment: fixed;
}

h1 {
    font-size: 24px;
    margin-bottom: 15px;
    color: #000000;
    text-align: center;
}

h2 {
    font-size: 20px;
    margin: 20px 0 10px;
    color: #000000;
    text-align: center;
}